#areaPhotos {
    width: 100%;
    height: auto;
    margin-top: 70px;
    padding: 10px;
    color: #fff;
    margin-bottom: 300px;
  }
  
  #titleAreaPhotos h1 {
    text-align: center;
    font-size: 50px;
    font-weight: 500;
    letter-spacing: 5px;
    text-transform: uppercase;
  }
  
  #searchPhotos {
    width: inherit;
    height: auto;
    display: flex;
    align-items: center;
    padding: 20px;
    gap: 8px;
  }
  
  #searchPhotos input {
    padding: 5px 20px 5px 20px;
    flex: 4;
    outline: none;
    border: none;
    font-size: 18px;
    background-color: rgba(255, 255, 255, 0.185);
    border-bottom: 2px purple solid;
    border-radius: 8px;
    color: #fff;
  }
  #searchPhotos label {
    font-size: 20px;
  }
  #searchPhotos button {
    padding: 8px;
    flex: 1;
    outline: none;
    border: none;
    cursor: pointer;
    font-size: 18px;
    border-radius: 8px;
    font-weight: bold;
    letter-spacing: 3px;
    background-color: rgba(255, 255, 255, 0.733);
  }
  .alerts {
    width: 100%;
    height: 50px;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    /* font-size: 20px; */
    /* text-transform: uppercase; */
    letter-spacing: 2px;
    color: gray;
  }

  #errorAlert {
    font-size: 20px;
    color: red;
  }
  
  .gridPhotos {
    width: 100%;
    height: auto;
    display: grid;
    grid-auto-columns: 1fr;
    grid-template-rows: 1fr;
    gap: 10px;
    justify-items: center;
    grid-auto-flow: row;
    grid-template-areas:
      "p0 p1 p2 p3"
      "p4 p5 p6 p7"
      "p8 p9 p10 p11"
      "p12 p13 p14 p15"
      "p16 p17 p18 p19"
      "p20 p21 p22 p23"
      "p24 p25 .  .";
  }

  .boxPhoto {
    border-radius: 20px;
    width: 430px;
    height: 430px;
    position: relative;
  }
  
  .boxPhoto img {
    position: absolute;
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    padding: 10px;
    cursor: pointer;
  }
  
  .boxPhoto img:hover {
    padding: 0px;
    transition: all ease-in-out 0.3s;
    opacity: 0.4;
  }
  
  .modal {
    background-color: rgba(0, 0, 0, 0.897);
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    position: fixed;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .btnCloseModal {
    outline: none;
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    padding: 5px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    color: red;
  }
  
  .btnCloseModal:hover {
    color: rgb(250, 152, 152);
  }
  
  .infoPhoto {
    width: 50%;
    height: 95%;
    background-color: rgb(52, 34, 75);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
  }
  
  .photoSelected {
    width: 100%;
    height: 80%;
    padding: 10px;
  }
  
  .photoSelected img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 10px;
  }
  
  .photoDescription {
    width: 100%;
    height: 20%;
    padding-left: 20px;
  }
  
  .photoDescription p {
    padding-top: 10px;
    text-transform: capitalize;
    font-size: 18px;
  }
  
  .photoDescription strong {
    letter-spacing: 1px;
    text-transform: uppercase;
  }
  
  /* =============================================================== */
  
  @media (width: 1366px) {
    #titleAreaPhotos h1 {
      font-size: 40px;
    }
  
    #searchPhotos input {
      font-size: 16px;
    }
    #searchPhotos label {
      font-size: 18px;
    }
    #searchPhotos button {
      font-size: 16px;
    }
  
    .alerts {
      font-size: 17px;
    }
    #errorAlert {
      font-size: 20px;
    }
    .gridPhotos {
      grid-template-areas:
        "p0 p1 p2"
        "p3 p4 p5 "
        "p6 p7 p8 "
        "p9 p10 p11 "
        "p12 p13 p14 "
        "p15 p16 p17 "
        "p18 p19 p20"
        "p21 p22 p23"
        "p24 p25 . ";
    }
    .boxPhoto {
      width: 400px;
      height: 400px;
    }
  
    .photoDescription p {
      font-size: 16px;
    }
  }
  
  @media screen and (max-width: 660px) {
    #titleAreaPhotos h1 {
      font-size: 25px;
    }
  
    #searchPhotos {
      gap: 5px;
    }
    .alerts {
      font-size: 12px;
      letter-spacing: 1px;
    }
    #errorAlert {
      font-size: 15px;
    }
  
    #searchPhotos input {
      font-size: 12px;
      text-align: start;
    }
    #searchPhotos label {
      display: none;
    }
  
    #searchPhotos button {
      font-size: 12px;
      letter-spacing: 1px;
    }
  
    .gridPhotos {
      grid-auto-columns: 1fr;
      grid-template-rows: 1fr;
      grid-template-areas:
        "p0"
        "p1"
        "p2"
        "p3"
        "p4"
        "p5"
        "p6"
        "p7"
        "p8"
        "p9"
        "p10"
        "p11"
        "p12"
        "p13"
        "p14"
        "p15"
        "p16"
        "p17"
        "p18"
        "p19"
        "p20"
        "p21"
        "p22"
        "p23"
        "p24"
        "p25";
    }
    .boxPhoto {
      width: 90%;
      height: 300px;
    }
  
    .btnCloseModal {
      top: 20%;
      right: 20px;
    }
  
    .infoPhoto {
      width: 90%;
      height: 50%;
    }
  
    .photoSelected {
      width: 100%;
      height: 70%;
      padding: 5px;
    }
  
    .photoDescription {
      width: 100%;
      height: 30%;
      padding-left: 20px;
    }
  
    .photoDescription p {
      padding-top: 2px;
      font-size: 10px;
    }
  }
  
  @media (min-width: 660px) and (max-width: 768px) {
    #titleAreaPhotos h1 {
      font-size: 40px;
    }
  
    #searchPhotos {
      padding: 30px;
    }
  
    .alerts {
      font-size: 14px;
      letter-spacing: 2px;
    }
    #errorAlert {
      font-size: 15px;
    }
  
    #searchPhotos input {
      font-size: 12px;
    }
    #searchPhotos label {
      font-size: 13px;
    }
  
    #searchPhotos button {
      font-size: 13px;
      letter-spacing: 1px;
    }
  
    .gridPhotos {
      gap: 8px;
      grid-template-areas:
        "p0 p1"
        "p2 p3"
        "p4 p5"
        "p6 p7"
        "p8 p9"
        "p10 p11"
        "p12 p13"
        "p14 p15"
        "p16 p17"
        "p18 p19"
        "p20 p21"
        "p22 p23"
        "p24 p25";
    }
    .boxPhoto {
      width: 350px;
      height: 350px;
    }
  
    .infoPhoto {
      width: 90%;
      height: 80%;
    }
  
    .photoSelected {
      width: 100%;
      height: 80%;
      padding: 8px;
    }
  
    .photoDescription p {
      padding-top: 2px;
      font-size: 12px;
    }
  }
  
  @media (min-width: 768px) and (max-width: 1280px) {
    .gridPhotos {
      gap: 4px;
      grid-template-areas:
        "p0 p1"
        "p2 p3"
        "p4 p5"
        "p6 p7"
        "p8 p9"
        "p10 p11"
        "p12 p13"
        "p14 p15"
        "p16 p17"
        "p18 p19"
        "p20 p21"
        "p22 p23"
        "p24 p25";
    }
  
    .infoPhoto {
      width: 90%;
      height: 80%;
    }
    .photoDescription p {
      font-size: 22px;
    }
  
    .photoSelected {
      width: 100%;
      height: 80%;
      padding: 8px;
    }
  
    .btnCloseModal {
      top: 30px;
      right: 45px;
    }
    .boxPhoto {
      width: 350px;
      height: 350px;
    }
  
    .alerts {
      font-size: 14px;
      letter-spacing: 1px;
      height: 10px;
    }
    #errorAlert {
      font-size: 14px;
    }
  
    #searchPhotos input {
      font-size: 12px;
    }
    #searchPhotos label {
      font-size: 14px;
    }
  
    #searchPhotos button {
      font-size: 12px;
  
      padding: 8px;
      letter-spacing: 1px;
    }
  }
  